<template>
  <div>
    <el-card shadow="never">
      <el-select v-model="namespace" @click.native="getNamespace" @change="selectedNamespace" filterable :placeholder="$t('please_select_namespace')">
        <el-option
            v-for="item in namespaces"
            :label="item"
            :value="item"
            :key="item"
        ></el-option>
      </el-select>
      &nbsp;&nbsp;
      <el-select :placeholder="$t('please_select_pod')" v-model="pod" @change="selectedPod">
        <el-option v-for="item in pods" :label="item.label" :value="item.value" :key="item.value"></el-option>
      </el-select>
      &nbsp;&nbsp;
      <el-select :placeholder="$t('please_select_container')" multiple v-model="container">
        <el-option :label="$t('check_all')" value="all"></el-option>
        <el-option v-for="item in containers" :label="item.label" :value="item.value" :key="item.value"></el-option>
      </el-select>
      &nbsp;&nbsp;
      <el-input v-model="destPath" style="width: 217px;height: 40px" autocomplete="off" :placeholder="$t('please_input_dest_path')"></el-input>

    </el-card>
  </div>
</template>

<script>
import { GetNamespace } from '@/api/namespaces'
import {GetPods} from "@/api/pods";

export default {
  data() {
    return {
      namespace: "",
      pod: "",
      container: [],
      namespaces:[],
      pods: [],
      containers:[],
      destPath:"",
    }
  },
  methods: {
    getNamespace() {
      GetNamespace().then(res => {
        if (res) {
          this.namespaces = []
          this.pod = ""
          this.pods = []
          this.container = []
          this.containers = []
          this.shell = ""
          const data = res.items;
          for (const key in data) {
            this.namespaces.push(data[key].metadata.name)
          }
          console.log(this.namespaces)
        }
      })
    },
    selectedNamespace() {
      GetPods({namespace: this.namespace}).then(res => {
        if (res) {
          const pods = [];
          this.pod = ""
          this.pods = []
          this.container = []
          this.containers = []
          const data = res.items;
          for (const key in data) {
            const _d = {label: data[key].metadata.name, value: data[key].metadata.name}
            pods.push(_d)
          }
          this.pods = pods
          console.log(this.pods)
        }
      })
    },
    selectedPod() {
      GetPods({namespace: this.namespace, pod: this.pod}).then(res => {
        if (res) {
          console.log(res)
          const containers = []
          this.container = []
          this.containers = []
          const data = res.spec.containers
          for (const key in data) {
            const _d = {label: data[key].name, value: data[key].name}
            containers.push(_d)
          }
          this.containers = containers
          console.log(this.containers)
        }
      })
    }
  },
  watch:{
    container:function(val,oldVal){
      let index =  val.indexOf('all'),oldIndex =  oldVal.indexOf('all');
      if(index!==-1 && oldIndex===-1 && val.length>1)
        this.container=['all'];
      else if(index!==-1 && oldIndex!==-1 && val.length>1)
        this.container.splice(val.indexOf('all'),1)
    }
  },
}
</script>